.App {
  text-align: center;
  background-color: #282c34;
  size: inherit;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  background-color: #282c34;
}
.board-holder { 
  position: relative;
  border: 2px solid #c5c5c5;
  margin: auto;
  margin-top: 35px;
  width: 720px;
  height: 720px;
}
.cell-holder {
  position: absolute;
  width: 90px;
  height: 90px;
  display: inline-block;
  /* box-sizing: border-box; */

}
.cell-holder:hover {
  border: 3px solid white;
  box-sizing: border-box;
  cursor: pointer;
}
.piece-img-wrapper {
  margin: auto;
  display: table-cell;
}
.piece-img {
  width: 100%;
}
.board-cell {
  position: absolute;
  /* background-color: bisque; */
  width: 90px;
  height: 90px;
  z-index: -10;
}